// Banner01.jsx
import React from 'react';
import PropTypes from 'prop-types';
import './styles.scss';
import bg from '../../assets/images/OrcaXP/bg1.png';

// 거래소 로고
import DexLogo from '../../assets/images/logo/dexscreener-logo.svg';
import PumpLogo from '../../assets/images/logo/pump-logo.png';
import JupiterLogo from '../../assets/images/logo/jupiter-logo.svg';

Banner01.propTypes = { data: PropTypes.array };

function MCMenu() {
  return (
    <div className="mc-root">
      <div className="mc-menu">
        {/* ── 거래소 버튼 3개 (가운데 세로 스택) */}
        <a
          className="mc-button full mc-brand is-orca"
          href="https://pump.fun/coin/GqNCn2mu9fCqw58DzATM2A837XGpiV4xjJaBRvixpump"
          target="_blank"
          rel="noopener noreferrer"
        >
          <span className="title">
            <img src={PumpLogo} alt="Orca" />
            <span className="label">Trade on Pump.fun</span>
          </span>
        </a>

        <a
          className="mc-button full mc-brand is-dex"
          href=""
          target="_blank"
          rel="noopener noreferrer"
        >
          <span className="title">
            <img src={DexLogo} alt="Dexscreener" />
            <span className="label">View on Dexscreener</span>
          </span>
        </a>

        <a
          className="mc-button full mc-brand is-jup"
          href=""
          target="_blank"
          rel="noopener noreferrer"
        >
          <span className="title">
            <img src={JupiterLogo} alt="Jupiter" />
            <span className="label">Swap on Jupiter</span>
          </span>
        </a>

        {/* ── Options / Quit (더블 버튼) */}
        <div className="double">
          <button className="mc-button full" type="button">
            <span className="title">MICO</span>
          </button>
          <button className="mc-button full" type="button">
            <span className="title">BUY</span>
          </button>
        </div>

        {/* ── Lang 버튼 */}
        <button
          className="mc-button full lang"
          type="button"
          aria-label="Change Language"
          title="Language"
        >
          <span className="title">
            <img
              src="https://i.ibb.co/99187Lk/lang.png"
              alt="Lang"
              width={26}
              height={26}
              loading="lazy"
            />
          </span>
        </button>
      </div>
    </div>
  );
}

function Banner01() {
  return (
    <section
      className="tf-slider tf-slider--mc"
      style={{
        backgroundImage: `url(${bg})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        position: 'relative',
      }}
    >
      <div className="bg-inner" />
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-12">
            <div className="tf-slider-item style-2 mc-center">
              <MCMenu />

              {/* 🔥 애니메이션 텍스트 추가 */}
              <div id="animation" className="element-animation">
                Minecoin ! Minecoin !
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Banner01;
