import React, { useState, useEffect } from 'react';
import './styles.scss';
import { Link } from 'react-router-dom';

import logo from '../../assets/images/logo/logo-footer.png';
import PumpLogo from '../../assets/images/logo/pump-logo.png';
import DexLogo from '../../assets/images/logo/dexscreener-logo.svg';
import JupiterLogo from '../../assets/images/logo/jupiter-logo.svg';

function Footer() {
  const [isVisible, setIsVisible] = useState(false);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  useEffect(() => {
    const toggleVisibility = () => setIsVisible(window.pageYOffset > 500);
    window.addEventListener('scroll', toggleVisibility);
    return () => window.removeEventListener('scroll', toggleVisibility);
  }, []);

  return (
    <footer className="footer">
      <div className="footer-inner">
        <div className="tf-container">
          <div className="row">
            {/* 좌측: 로고 + 텍스트 */}
            <div className="widget widget-infor">
              <div className="logo">
                <img id="logo_footer" src={logo} alt="OrcaXP" />
              </div>
            </div>

            {/* 우측: 거래소 로고 */}
            <div className="exchange-logos">
              <a href="https://pump.fun/coin/GqNCn2mu9fCqw58DzATM2A837XGpiV4xjJaBRvixpump" target="_blank" rel="noopener noreferrer">
                <img src={PumpLogo} alt="Pump" />
              </a>
              <a href="" target="_blank" rel="noopener noreferrer">
                <img src={DexLogo} alt="Dexscreener" />
              </a>
              <a href="" target="_blank" rel="noopener noreferrer">
                <img src={JupiterLogo} alt="Jupiter" />
              </a>
            </div>
          </div>
        </div>
      </div>

      {/* 하단 copyright + 소셜 */}
      <div className="bottom-inner">
        <div className="tf-container">
          <div className="row">
            <div className="col-md-12">
              <div className="bottom">
                <p className="copy-right">MINE COIN 2025 - ALL rights reserved</p>

                <ul className="social-item">
                  <li>
                    <Link to="https://x.com/minecoin311">
                      <i className="fa-brands fa-x-twitter"></i>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      {isVisible && <Link onClick={scrollToTop} to="#" id="scroll-top"></Link>}
    </footer>
  );
}

export default Footer;
